.table {
  border: 1px solid #eeeeee;
}

.table-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #333333;
  color: #fff;
  padding: 5px;
  justify-content: space-evenly;
  justify-self: left;
  justify-items: unset;
}
.post {
  padding: 3px;
  display: flex;
  flex-direction: row;
}

.new-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #eeeeee;
  color: #000;
  padding: 5px;
  justify-content: space-evenly;
  justify-items: unset;
}
